@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #161623;
}
body::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#f00,#f0f);
  -webkit-clip-path: circle(30% at right 70%);
          clip-path: circle(30% at right 70%);
}
body::after{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#2196f3,#e91e63);
  -webkit-clip-path: circle(20% at 10% 10%);
          clip-path: circle(20% at 10% 10%);
}
.home{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  z-index: 1;
}
.home .cards{
  position: relative;
  width: 250px;
  height: 300px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;border-top: 1px solid ;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}
.home .cards .contents{
  padding: 20px;
  text-align: center;
  transition: 0.5s;
}
.home .cards .contents h2{
  position:absolute;
  top: -30px;
  right: 10px;
  font-size: 6em;
  color: rgba(255, 255, 255, 0.10);
  pointer-events: none;
}
.home .cards .contents h3{
  color: #fff;
  font-size: 1.8em;
  margin-top: 60px;
  z-index: 1;
}
.home .cards .contents p{
  font-size: 1em;
  color: #fff;
  font-weight: 300;
  margin-top: 5px;
}
.home .cards .contents a{
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  margin-top: 30px;
  background: #fff;
  color: #000;
  border-radius: 20px;
  text-decoration: none;
  font-weight: 500;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  transition: 0.8s;
}
.header h1{
  margin-left: 300px;
  margin-top: -200px;
  color: #fff;
  font-size: 5em;
  padding: 70px;
}
.footer h6{
  position: absolute;
  bottom: 0;
  left: 10px;
  color: #fff;
  font-size: 2em;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: radial-gradient( #535381, #161623);
}
.card{
    position: relative;
    width: 340px;
    height: 450px;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
}
.circle{
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: -200px;
    position: absolute;
    width: 500px;
    height: 500px;
    background: linear-gradient(rgb(0, 0, 0), rebeccapurple);
    -webkit-clip-path: circle();
            clip-path: circle();
}
.circle .imgBg{
    position: absolute;
    left: 50%;
    bottom: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 340px;
    height: 340px;
}
.circle img{
    position: absolute;
    height: 15%;
    width: 15%;
    top: 250px;
    left: 135px;
}
.circle h6{
    position: absolute;
    top: 345px;
    left: 110px;
    font-size: 1.5em;
    color: #fff;
}
.circle h3{
    position: absolute;
    top: 375px;
    left: 150px;
    font-size: 1.5em;
    color: #fff;
}
.circle .imgBg img{
    position: absolute;
    top: 50px;
    left: 75px;
    width: 90%;
    height: 90%;
    object-fit: contain;
}
.description{
    position: absolute;
    top: 430px;
    margin-left: 620px;
    left: 0;
}
.description img{
    width: 7.5%;
    height: 7.5%;
    padding: 0;
}
.description h1{
    font-size: 1.6em;
}
.description h2{
    font-size: 1.4em;
}
.count{
    height: 160px;
    width: 150px;
    position: absolute;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    background: darkorchid;
    border-radius: 5px;
    box-shadow: 0 20px 35px rgba(0,0,0,0.3);
    overflow: hidden;
    font-family: 'Poppins',sans-serif;
    color: #ffffff;
    font-size: 20px;
    letter-spacing: 6px;
    display: grid;
    place-items: center;
}
.b-inc{
    right: 100px;
}
.b-dec{
    left: 100px;
}

.form-card{
  position: relative;
  width: 400px;
  height: 500px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: inline-block;
  justify-content: center;
  align-items: center;
  border-top: 1px solid ;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  padding: 50px 35px;
  z-index: 1;
}
.form-card *{
    color: #ffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}
.form-card h3{
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
}
.form-card h3 span{
    display: block;
    font-size: 16px;
    font-weight: 300px;
    color: #e5e5e5;
}
label{
    display: block;
    margin-top: 20px;
    font-size: 16px;
    font-weight: 500; 
}
input{
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border: none;
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 10px;
}
::-webkit-input-placeholder{
    color: #e5e5e5;
}
:-ms-input-placeholder{
    color: #e5e5e5;
}
::placeholder{
    color: #e5e5e5;
}
::-webkit-input-placeholder :checked{
    display: none;
}
:-ms-input-placeholder :checked{
    display: none;
}
::placeholder :checked{
    display: none;
}
.submit{
    width: 100%;
}
